import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/large-page',
      name: 'LargePage',
      component: (resolve) => require(['../views/LargePage'], resolve)
    }
  ]
})



